.head-container {
    padding-top: 300px;
    padding-bottom: 200px;
  }
  .head-second{
    width: 1000px;
    display: flex;
    justify-content: center;
  }
  .head-text {
    padding-right: 200px;
    align-self: center;
  }
  .head-text h3 {
    color: #ffffff;
  }
  .head-text h1 {
    color: #00ffff;
    font-size: 65px;
    font-weight: 800;
  }
  .head-botom {
    padding-top: 20px;
  }
  
  .button-28 {
    appearance: none;
    background-color: transparent;
    border: 2px solid #00ffff;
    border-radius: 15px;
    box-sizing: border-box;
    color: #00ffff;
    cursor: pointer;
    display: inline-block;
    font-family: Roobert, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica,
      Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 18px;
    font-weight: 600;
    line-height: normal;
    min-height: 30px;
    min-width: 100px;
    outline: none;
    padding: 6px 12px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: 130px;
    height: 40px;
    will-change: transform;
  }
  
  .button-28:disabled {
    pointer-events: none;
  }
  
  .button-28:hover {
    color: #000000;
    background-color: #00ffff;
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    transform: translateY(-2px);
  }
  .button-28:active {
    box-shadow: none;
    transform: translateY(0);
  }
  
  .head-container {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #081b29;
  }
  
  .container-img {
    background-color: #01d6d6;
    border-radius: 50% 27% 51% 28% / 44% 25% 57% 34%;
    width: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
  }
  
  .head-img img {
    width: 330px;
    border-radius: 50% 27% 51% 28% / 44% 25% 57% 34%;
  }